code.autosize.zh = "\n* \u8BBE\u7F6E\u5C5E\u6027 `autosize`\uFF0C\u6587\u672C\u57DF\u4F1A\u81EA\u52A8\u9002\u5E94\u9AD8\u5EA6\u7684\u53D8\u5316(\u53EA\u5BF9textarea\u6709\u6548)\u3002\n```javascript\n<template>\n  <div class=\"input-warp\">\n    <qc-input v-model=\"value2\" type=\"textarea\" placeholder='\u8BF7\u8F93\u5165...' :border-radius=\"true\" :autosize=\"true\">\n    </qc-input>\n  </div>\n</template>\n<script>\n  export default {\n    data () {\n      return {\n        value2: ''\n      }\n    }\n  }\n</script>\n```\n";
