code.overview.zh = "\n* `sku`\u662F\u4E00\u4E2A\u6BD4\u8F83\u7279\u6B8A\u7684\u7528\u4E8E\u5546\u57CE\u7684\u7EC4\u4EF6\uFF0C\u4F20\u7EDF\u5F62\u5F0F`sku`\u662F\u901A\u8FC7\u6BCF\u9009\u4E00\u4E2A\u89C4\u683C\u5BF9\u5E94\u8BF7\u6C42\u4E00\u6B21\u540E\u53F0\u3002\n* \u8FD9\u91CC\u5C06\u5176\u5C01\u88C5\u6210\u4E00\u4E2A\u524D\u7AEF\u7EC4\u4EF6\uFF0C\u540E\u53F0\u53EA\u9700\u8981\u4E00\u6B21\u8FD4\u56DE\u6240\u6709\u89C4\u683C\u7EC4\u5408\u5BF9\u5E94\u7684\u4EF7\u683C\uFF0C\u5E93\u5B58\uFF0CskuId\u7B49\u6570\u636E\u5373\u53EF\u3002\n* \u7531\u4E8E`sku`\u754C\u9762UI\u8BBE\u8BA1\u4E0D\u4E00\u81F4\uFF0C\u8FD9\u91CC\u53EA\u63D0\u4F9B\u65B9\u6CD5\uFF0C\u7528\u6237\u9700\u8981\u6839\u636EUI\u56FE\u91CD\u65B0\u8BBE\u8BA1\u5BF9\u5E94\u7684\u6837\u5F0F\uFF0C\u76EE\u524D\u53C2\u8003\u4E86\u4EAC\u4E1C\u548C\u6DD8\u5B9D\u7684\u4EA4\u4E92\u65B9\u5F0F \n";

